@import './_variables.sass';

@font-face {
    font-family: 'Helvetica';
    src: url('../fonts/Helvetica/Helvetica.ttf'); }

%heading--primary {
    font-size: 3rem;
    font-weight: 700;
    line-height: 3.125rem;
    font-family: "Helvetica", sans-serif; }

%heading--secondary {
    font-family: "Helvetica", sans-serif;
    font-size: 2.875rem;
    line-height: 3.125rem;
    font-weight: 700; }

a.reference--green {
    color: $lightgreen;
    transition: color .2s ease;
    &:hover {
        color: $black; } }

body {
    font-family: "Open Sans", sans-serif;
    header {
        padding: 3.125rem 0;
        background-color: $background_light;
        .header {
            &__logo_wrapper {
                width: auto;
                max-height: 3.125rem;
                img.header__logo {
                    object-fit: contain; } }
            &__navigation {
                .navigation__list {
                    .navigation__item {
                        position: relative;
                        &:hover {
                            a::after {
                                transform: scaleX(1); } }
                        a {
                            text-transform: capitalize;
                            font-family: "Helvetica", sans-serif;
                            font-size: 1.0625rem;
                            line-height: 0.875rem;
                            color: $black;
                            &::after {
                                content: '';
                                width: 100%;
                                left: 0;
                                position: absolute;
                                bottom: -0.1875rem;
                                border-top: 2px solid $black;
                                transition: transform 0.3s ease-out;
                                transform: scaleX(0);
                                animation-direction: alternate; } }
                        .underline {
                            width: 0;
                            height: 0; }
                        &-button {
                            border: none;
                            border-radius: 0.25rem;
                            display: inline-block;
                            padding: 0.725rem 1.325rem;
                            background-color: $lightgreen;
                            color: $white;
                            outline: none; } } } } } }

    section.top {
        padding: {
            top: 2rem;
            bottom: 5rem; }
        margin-bottom: 3rem;
        background-color: $background-light;
        clip-path: ellipse(90% 90% at 50% 8%);
        .top {
            &__heading {
                @extend %heading--primary;
                margin-bottom: 1.3125rem; }
            &__description {
                font-size: 1.125rem;
                line-height: 1.6875rem;
                margin-bottom: 1.6875rem; }
            &__video {
                display: block;
                margin: auto; }
            &__heading,
            &__description {
                margin: {
                    left: auto;
                    right: auto; }
                max-width: 46.875rem;
                text-align: center; } } }

    section.organizations {
        margin-bottom: 10rem;
        h2.organizations__heading {
            @extend %heading--secondary;
            margin-bottom: 1.0625rem; }
        h3.organizations__description {
            font-size: 1rem;
            line-height: 1.5rem;
            margin-bottom: 1.875rem; }
        h2.organizations__heading,
        h3.organizations__description {
            text-align: center; }
        .organizations__image_wrapper {
            width: auto;
            max-height: 9.375rem;
            img.organizations__image {
                max-width: 100%;
                object-fit: contain;
                display: block;
                margin: auto; } } }

    section.welcome {
        margin-bottom: 5rem;
        h2.welcome__heading {
            @extend %heading--secondary;
            margin-bottom: 1.6875rem; }
        .welcome__content {
            p.welcome__text {
                line-height: 1.5rem; } } }

    section.styles {
        background-color: $background_lightblue;
        padding: 4.6875rem 0;
        margin-bottom: 5rem;
        .styles {
            &__heading {
                @extend %heading--secondary;
                margin-bottom: 1.0625rem; }
            &__description {
                line-height: 1.5rem;
                margin-bottom: 3.125rem; } }
        .row {
            margin-bottom: 2.5rem;
            &:last-of-type {
                margin-bottom: 0; }
            .styles__items {
                width: 100%;
                li.styles__item {
                    max-width: 22.5rem;
                    h4.item__heading {
                        font-weight: 600;
                        font-size: 1.25rem;
                        line-height: 1.875rem;
                        margin-bottom: 0.4375rem; }
                    h5.item__description {
                        font-size: 0.875rem;
                        line-height: 1.5rem;
                        margin-bottom: 1.25rem; }
                    h4.item__heading,
                    h5.item__description {
                        text-align: center; }
                    .item__image {
                        display: block;
                        width: 22.5rem;
                        height: 12.5rem;
                        background-image: url('../images/styles_item.png');
                        background-repeat: no-repeat;
                        background-size: 100%;
                        background-position: center;
                        position: relative;
                        transition: background-size .5s ease;
                        &:hover {
                            background-size: 125%; }
                        .image__play {
                            opacity: 0.8;
                            top: 50%;
                            left: 50%;
                            width: 4.375rem;
                            height: 4.375rem;
                            transform: translate(-50%, -50%);
                            position: absolute;
                            &::before {
                                content: '';
                                position: absolute;
                                width: 100%;
                                height: 100%;
                                background-color: $lightgreen;
                                background-repeat: no-repeat;
                                background-size: cover;
                                border-radius: 50%; }
                            &::after {
                                content: '';
                                position: absolute;
                                top: 50%;
                                left: calc(50% - .25rem);
                                transform: translate(-50%, -50%) rotate(45deg);
                                border: {
                                    top: .5rem solid $white;
                                    right: .5rem solid $white;
                                    bottom: .5rem solid transparent;
                                    left: .5rem solid transparent; } } } } } } } }
    section.difference {
        .difference {
            &__heading {
                @extend %heading--secondary;
                margin-bottom: 1.0625rem; }
            &__description {
                line-height: 1.5rem;
                margin-bottom: 3.125rem;
                a.difference__contact {
                    color: $lightgreen;
                    transition: color .2s ease;
                    &:hover {
                        color: $black; } } } }
        .difference__items {
            width: 100%;
            .difference__item {
                max-width: 15.625rem;
                h4.item__heading {
                    margin-top: 0.5rem;
                    font-weight: 600; }
                h4.item__heading,
                h5.item__description {
                    font-size: 0.875rem;
                    line-height: 1.5rem; } } } } }
